import React, {useEffect, useState} from "react";
import { DragDropContext } from "react-beautiful-dnd";
import uuid from "react-uuid";
import {onDragEnd} from "./onDragEnd";
import Column from "./Column/Column";
import styled from "styled-components";
import {useHistory} from 'react-router-dom'
import {useDispatch, useSelector} from "react-redux";
import {addCol, addTaskToColumns, delCol, getCurrentBoard} from "../../redux/actions/curBoard";


const Board = () => {
    const dispatch = useDispatch()
    const history = useHistory()
    const boardId = history.location.pathname.slice(8)
    const [isLoading, setIsLoading] = useState(true)

    useEffect(() => {
        dispatch(getCurrentBoard(boardId))
        setTimeout(() => {
            setIsLoading(false)
        },1000)
    }, [])
    const columns = useSelector(state => state.curBoard.columns)

    const addTaskHandler = (colId) => {
        dispatch(addTaskToColumns(colId))
    }

    const delColHandler = (colId) => {
        dispatch(delCol(boardId,colId))
    }
    const addColHandler = () => {
        const col = {
            id: uuid(),
            columnName: 'newCol',
            tasks: []
        }
        dispatch(addCol(boardId, col))
    }
    return (
        <>
            {
                isLoading
                    ? <div>...loading</div>
                    : <BoardContainer>
                        <DragDropContext
                            onDragEnd={result => onDragEnd(result, columns)}
                        >
                            {columns.map((col, index) => {
                                return <Column
                                    columnName={col.columnName}
                                    delCol={delColHandler}
                                    addTask={addTaskHandler}
                                    col={col}
                                    key={index}
                                />
                            })}
                            <AddColBtn onClick={addColHandler}>+</AddColBtn>
                        </DragDropContext>
                    </BoardContainer>
            }
        </>
    );
}
export default Board;

const AddColBtn = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    border-radius: 100px;
    color: black;
    background: lightgrey;
    cursor: pointer;
`

const BoardContainer = styled.div`
   marginLeft: 20px; 
   display: flex; 
   justifyContent: flex-start;
   height: 80%; 
   overflow: scroll;
   
   &::-webkit-scrollbar {
        width: 0px;
        background: transparent;
   }
`
